import icon_clmm from '@/assets/images/icon_clmm.png'
import icon_dlmm from '@/assets/images/icon_dlmm.png'
import icon_positions from '@/assets/images/icon_positions@2x.png'
import pool_bg from '@/assets/images/pool_bg.png'
import usePoolsStore from '@/store/pool'
import useCreatePoolStore from '@/store/pool/useCreatePool'
import useDlmmPoolsStore from '@/store/pool/useDlmmPoolStore'
import usePositionStore from '@/store/position'
import useStatsStore from '@/store/stats'
import { Block, SelectTab } from '@cetus/design'
import useQueryParams from '@cetus/hooks/src/useQueryParams'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { VTextLabelBox } from '@cetus/ui-kit'
import { d } from '@cetus/utils'
import { Box, Button, HStack, Image, Text, Tooltip, VStack } from '@chakra-ui/react'
import { useMemo, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import LpBurnModal from '../position/common/LpBurnModal'
import VolChartPageBlock from '../stats/VolChartPageBlock'

function PoolsTopData({ isRefreshed }: { isRefreshed: boolean }) {
  const { tab } = useQueryParams()
  const navigate = useNavigate()
  const [isOpenLpBurnModal, setIsOpenLpBurnModal] = useState<boolean>(false)
  const { statisticsData } = useStatsStore()
  const { poolListLength, setPoolListLength } = usePoolsStore()
  const { dlmmPoolListLength } = useDlmmPoolsStore()
  const { showPosListLength } = usePositionStore()
  const { setPoolType, setCurrentStep } = useCreatePoolStore()
  const { isApp } = useWindowWidth()

  const totalPositionsNum = d(showPosListLength?.clmm_position_count ?? 0)
    .plus(showPosListLength?.dlmm_position_count ?? 0)
    .toString()

  const tabList = useMemo(() => {
    console.log('🚀 ~ PoolsTopData ~ poolListLength:', poolListLength)
    return [
      {
        label: 'CLMM',
        value: 'clmm_pools',
        num: tab === undefined || tab === 'clmm_pools' ? poolListLength : '',
        imgInfo: isApp
          ? undefined
          : {
              src: icon_clmm,
              w: isApp ? '16px' : '22px',
              h: isApp ? '16px' : '22px'
            }
      },
      {
        label: 'DLMM',
        value: 'dlmm_pools',
        beta: true,
        betaUrl: '/images/icon_beta@2x.png',
        num: tab === 'dlmm_pools' ? dlmmPoolListLength : '',
        imgInfo: isApp
          ? undefined
          : {
              src: icon_dlmm,
              w: isApp ? '16px' : '22px',
              h: isApp ? '16px' : '22px'
            }
      },
      {
        label: isApp ? 'Positions' : 'My Positions',
        value: 'positions',
        num: tab === 'positions' && d(totalPositionsNum).gt(0) ? totalPositionsNum : '',
        imgInfo: isApp
          ? undefined
          : {
              src: icon_positions,
              w: isApp ? '16px' : '22px',
              h: isApp ? '16px' : '22px'
            }
      }
    ]
  }, [poolListLength, dlmmPoolListLength, showPosListLength, tab, isApp])

  const [isTooltipOpen, setTooltipOpen] = useState(false)

  const onCreatePool = () => {
    if (tab === 'dlmm_pools') {
      setPoolType('dlmm')
      setCurrentStep(2)
    } else {
      setPoolType('clmm')
      setCurrentStep(2)
    }
    navigate('/create-pool')
  }

  const onAddLiquidity = () => {
    if (tab === 'dlmm_pools') {
      setPoolType('dlmm')
    } else {
      setPoolType('clmm')
    }
    navigate('/select-pool')
  }
  return (
    <VStack w={{ base: '100%', lg: '1160px' }}>
      <HStack
        w="100%"
        gap={{
          base: '12px',
          lg: '32px'
        }}
        flexDirection={{
          base: 'column',
          lg: 'row'
        }}
      >
        <VStack
          w={{
            base: '100%',
            lg: '395px'
          }}
          justify="space-between"
          align="flex-start"
          gap={{
            base: '12px',
            lg: '40px'
          }}
        >
          <Text fontSize="24px" fontWeight="500" color="text_caption">
            Liquidity Pools
          </Text>
          <VStack
            gap="68px"
            p="32px 40px"
            align={{
              base: 'center',
              lg: 'flex-start'
            }}
            borderRadius="16px"
            bgImage={pool_bg}
            bgPosition="center"
            w="100%"
            h="240px"
            backgroundSize="100% 100%"
          >
            <VTextLabelBox
              wrapStyle={{
                alignItems: isApp ? 'center' : 'flex-start',
                gap: '12px'
              }}
              titleStyle={{
                fontSize: '16px'
              }}
              valueStyle={{
                fontSize: '20px',
                fontWeight: '600'
              }}
              title="Total Value Locked"
              value={statisticsData?.summary?.totalTvl || ''}
              isLoading={!statisticsData}
            />
            <VTextLabelBox
              wrapStyle={{
                alignItems: isApp ? 'center' : 'flex-start',
                gap: '12px'
              }}
              titleStyle={{
                fontSize: '16px'
              }}
              valueStyle={{
                fontSize: '20px',
                fontWeight: '600'
              }}
              title="Cumulative Volume"
              value={statisticsData?.summary?.cumulativeVol || ''}
              isLoading={!statisticsData}
            />
          </VStack>
        </VStack>
        <Box
          h="100%"
          mb="-4px"
          w={{
            base: '100%',
            lg: 'calc(100% - 395px - 32px)'
          }}
        >
          <VolChartPageBlock statisticsData={statisticsData} pageFrom="pools" isRefresh={!isRefreshed} />
        </Box>
      </HStack>
      <HStack
        w="100%"
        mt="32px"
        justify="space-between"
        flexDirection={{
          base: 'column-reverse',
          lg: 'row'
        }}
      >
        <SelectTab
          type="borderTab"
          wrapStyle={{
            w: {
              base: '100%',
              lg: '547px'
            },
            h: '60px'
          }}
          itemStyle={{
            flex: isApp ? 'auto' : '1',
            fontSize: isApp ? '15px' : '16px'
          }}
          betaStyle={{
            right: '-10px'
          }}
          tabList={tabList}
          currentTab={(tabList?.find(item => item?.value === tab)?.label as string) || 'CLMM'}
          handleChangeTab={(item: any) => {
            // if (item.value == 'positions') {
            //   navigate('/pools?tab=positions')
            // } else {
            //   navigate('/pools?tab=pools')
            // }
            navigate(`/pools?tab=${item.value}`)
          }}
        />
        <HStack w={{ base: '100%', lg: 'unset' }}>
          {tab == 'positions' ? (
            <Tooltip
              isOpen={isTooltipOpen}
              label={
                <Text fontSize="12px" lineHeight="20px">
                  Permanently burn/lock liquidity for an existing position. Trading fees and mining rewards earned remain claimable.
                </Text>
              }
              aria-label="Yield"
              placement="top"
            >
              <Button
                w={{ base: '50%', lg: '168px' }}
                fontWeight="500"
                variant="outline"
                as="div"
                borderRadius="8px"
                onMouseEnter={() => setTooltipOpen(true)}
                onMouseLeave={() => setTooltipOpen(false)}
                onClick={() => {
                  setTooltipOpen(false)
                  setIsOpenLpBurnModal(true)
                }}
              >
                LP Burn
              </Button>
            </Tooltip>
          ) : (
            <Button borderRadius="8px" w={{ base: '50%', lg: '168px' }} fontWeight="500" variant="outline" onClick={onCreatePool}>
              Create a new pool
            </Button>
          )}
          <Button borderRadius="8px" fontWeight="500" w={{ base: '50%', lg: '168px' }} onClick={onAddLiquidity}>
            Add Liquidity
          </Button>
        </HStack>
      </HStack>
      {isOpenLpBurnModal && <LpBurnModal isOpen={isOpenLpBurnModal} onClose={() => setIsOpenLpBurnModal(false)} />}
    </VStack>
  )
}

export default PoolsTopData

const PoolTabs = ({
  tabList
}: {
  tabList: {
    label: string
    value: string
    num: string
    imgInfo: {
      src: string
      w: string
      h: string
    }
  }[]
}) => {
  const { tab } = useQueryParams()
  const { isApp } = useWindowWidth()
  const navigate = useNavigate()
  return isApp ? (
    <HStack w="100%" gap="0" h="58px" borderRadius="12px" bg="bg_secondary" border="1px solid" borderColor="border">
      {tabList?.map(item => {
        const isActive = (tab ?? 'clmm_pools') === item?.value
        return (
          <VStack
            key={item?.label}
            justify="flex-start"
            flex="1"
            gap="4px"
            cursor="pointer"
            onClick={() => {
              navigate(`/pools?tab=${item.value}`)
            }}
            position="relative"
            sx={{
              _before: isActive
                ? {
                    content: "''",
                    width: '20px',
                    height: '2px',
                    background: 'primary',
                    position: 'absolute',
                    left: 'calc(50% - 10px)',
                    bottom: '-10px'
                  }
                : {}
            }}
          >
            <HStack>
              <Image src={item?.imgInfo?.src} w="16px" h="16px" />
              <Text fontSize="13px" color={isActive ? 'primary' : 'text_paragraph'}>
                {item?.label}
              </Text>
            </HStack>
            {item?.num ? (
              <Block w="unset" lineHeight="1" ml="8px" p="2px 6px" borderRadius="8px" fontSize="12px" color="primary">
                {item?.num}
              </Block>
            ) : (
              <Box as="div" h="18px" />
            )}
            <Box pos="absolute" w="20px" h="2px" bg="" />
          </VStack>
        )
      })}
    </HStack>
  ) : (
    <SelectTab
      type="borderTab"
      wrapStyle={{
        w: {
          base: '100%',
          lg: '547px'
        },
        h: '60px'
      }}
      itemStyle={{
        flex: isApp ? 'auto' : '1',
        fontSize: isApp ? '13px' : '16px'
      }}
      tabList={tabList}
      currentTab={(tabList?.find(item => item?.value === tab)?.label as string) || 'CLMM'}
      handleChangeTab={(item: any) => {
        // if (item.value == 'positions') {
        //   navigate('/pools?tab=positions')
        // } else {
        //   navigate('/pools?tab=pools')
        // }
        navigate(`/pools?tab=${item.value}`)
      }}
    />
  )
}
