import useCommonGlobalStore from '@/store/common/global'
import { DLMMPoolApiInfo } from '@/types'
import { DlmmApiPoolGroupItem } from '@/types/dlmm'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { H5MapTable, H5MapTableItem, Icon } from '@cetus/ui-kit'
import { transColumns2H5 } from '@cetus/ui-kit/src/components/H5MapTable/util'
import Pagination, { PaginationProps } from '@cetus/ui-kit/src/components/Pagination'
import { ColumnsType } from '@cetus/ui-kit/src/components/Table'
import { cancelBubble, d } from '@cetus/utils'
import { Box, Button, Center, Collapse, FlexboxProps, StackProps, Text, VStack } from '@chakra-ui/react'
import { useMemo, useState } from 'react'

function H5PoolsCard<T>({
  item,
  columns,
  goLiquidity,
  isOpen = false,
  onOpen,
  loading,
  itemSkeletonLength,
  itemHeight
}: {
  item: DlmmApiPoolGroupItem
  columns: any[]
  goLiquidity: (url: string, poolApiInfo: any) => void
  isOpen?: boolean
  onOpen: (groupId: string, isOpen: boolean) => void
  loading: boolean
  itemSkeletonLength?: number
  itemHeight?: FlexboxProps['flexBasis']
}) {
  const [showAll, setShowAll] = useState(false)
  const { setBackUrl } = useCommonGlobalStore()

  const onExpand = (e: any) => {
    cancelBubble(e)
    onOpen(item?.id, !!!isOpen)
    if (!isOpen) {
      setShowAll(false)
    }
  }
  const onShowAll = (e: any) => {
    cancelBubble(e)
    setShowAll(!showAll)
  }

  const allData = item?.list
  const lessData = item?.list?.slice(0, 3)

  const isOnlyOneData = useMemo(() => allData?.length === 1, [allData])

  const { isApp } = useWindowWidth()

  const startingHeight =
    lessData
      ?.map((l, index) => transColumns2H5<T>(columns, { ...l, hasFee: true }, index))
      ?.reduce(
        (acc, current) =>
          d(acc)
            .plus((current?.filter(i => !i?.hidden)?.length - 2) * 30)
            .plus(18)
            .plus(84)
            .plus((current?.filter(i => !i?.hidden)?.length - 1) * 8)
            .toNumber(),
        0
      ) +
    (lessData?.length - 1) * 8 +
    16

  const onClick = (e: any) => {
    if (isOnlyOneData) {
      setBackUrl('/pools?tab=dlmm_pools')
      goLiquidity(`/dlmm?poolId=${allData?.[0]?.poolId}`, item)
    } else {
      onExpand(e)
    }
  }

  return (
    <VStack borderRadius="12px" gap="0" bg={isOpen ? 'bg_secondary' : 'transparent'} w="100%">
      <H5MapTableItem
        data={
          loading
            ? []
            : transColumns2H5<T>(columns, {
                ...item,
                hasFee: isOnlyOneData,
                isParent: true,
                isOpen,
                onExpand: onClick,
                ...(isOnlyOneData ? allData?.[0] : {})
              } as any)
        }
        loading={loading}
        skeletonLength={itemSkeletonLength}
        onClick={onClick}
        itemHeight={itemHeight}
        style={{
          w: '100%',
          p: '8px',
          borderRadius: '12px',
          bg: isOpen ? 'text_highlight_opacity.10' : 'transparent'
        }}
      />
      {!isOnlyOneData && (
        <Collapse in={isOpen} animateOpacity transition={{ enter: { duration: 0.8 } }} style={{ width: '100%' }}>
          <>
            <Collapse startingHeight={startingHeight} in={showAll} style={{ width: '100%' }}>
              <VStack w="100%" p="8px" gap="8px">
                {Array.isArray(allData) &&
                  allData?.length > 0 &&
                  allData?.map((i, index) => {
                    return (
                      <H5MapTableItem
                        key={i?.poolId}
                        data={loading ? [] : transColumns2H5<T>(columns, { ...i, hasFee: true }, index)}
                        loading={loading}
                        skeletonLength={itemSkeletonLength}
                        onClick={() => {
                          setBackUrl('/pools?tab=dlmm_pools')
                          goLiquidity(`/dlmm?poolId=${i.poolId}`, i)
                        }}
                        style={{ w: '100%', border: '1px solid', borderColor: 'border', p: '8px', borderRadius: '12px' }}
                        itemHeight={itemHeight}
                      />
                    )
                  })}
              </VStack>
            </Collapse>
            {Array.isArray(allData) && allData?.length > 3 ? (
              <Center w="100%">
                <Button
                  h="44px"
                  variant="unstyled"
                  onClick={onShowAll}
                  display="flex"
                  gap="4px"
                  cursor="pointer"
                  _hover={{
                    p: {
                      color: 'primary'
                    },
                    svg: {
                      fill: 'primary'
                    }
                  }}
                >
                  <Text fontSize="12px" color={showAll ? 'text_highlight' : 'text_paragraph'}>
                    {showAll ? 'Show Less' : 'Show All'}
                  </Text>
                  <Icon xlinkHref="#icon-icon_arrow" transform={showAll ? 'rotate(180deg)' : 'rotate(0deg)'} fontSize="12px" />
                </Button>
              </Center>
            ) : (
              <Box h="9px" w="100%" />
            )}
          </>
        </Collapse>
      )}
    </VStack>
  )
}
interface H5PoolsProps<T> {
  dataSource: T[]
  itemSkeletonLength?: number
  skeletonLength?: number
  columns: ColumnsType<T>[]
  rowKey?: string | ((record: T) => string)
  onBodyHover?: () => void
  onBodyLeave?: () => void
  loading: boolean
  haveDividingLine?: boolean
  pagination?: PaginationProps
  wrapStyle?: StackProps
  itemHeight?: FlexboxProps['flexBasis']
  isShowBorder?: boolean
  isWatch?: boolean
  openPoolsGroup: Record<string, boolean> | undefined
  goLiquidity: (url: string, poolApiInfo: any) => void
  onOpenPoolsGroup: (groupId: string, isOpen: boolean) => void
}

function H5Pools<T>({
  dataSource,
  columns,
  itemSkeletonLength,
  skeletonLength = 3,
  rowKey,
  loading,
  pagination,
  wrapStyle,
  onBodyHover,
  onBodyLeave,
  itemHeight,
  isShowBorder = true,
  haveDividingLine = true,
  openPoolsGroup,
  goLiquidity,
  isWatch,
  onOpenPoolsGroup
}: H5PoolsProps<T>) {
  const skeletonData = new Array(skeletonLength).fill(0)
  const { setBackUrl } = useCommonGlobalStore()
  return isWatch ? (
    <H5MapTable<DLMMPoolApiInfo>
      rowKey="poolAddress"
      dataSource={dataSource}
      columns={columns}
      loading={loading}
      itemSkeletonLength={7}
      itemHeight="30px"
      rowStyle={(_, index) => ({
        w: '100%',
        p: '0px',
        mt: '0px'
      })}
      onRowClick={item => {
        setBackUrl('/pools')
        console.log(item, 'item')
        // navigate(`/liquidity?poolAddress=${item.poolAddress}`)
        goLiquidity(`/dlmm?poolId=${item.poolId}`, item)
      }}
    />
  ) : (
    <VStack
      w="100%"
      {...(wrapStyle || {})}
      onMouseEnter={() => onBodyHover && onBodyHover()}
      onMouseLeave={() => onBodyLeave && onBodyLeave()}
      border="1px solid"
      borderColor="border"
      borderRadius="12px"
    >
      {(dataSource && dataSource?.length && !loading ? dataSource : skeletonData)?.map((item, index) => (
        <VStack w="100%" key={loading ? index : typeof rowKey === 'function' ? rowKey(item) : rowKey ? (item as unknown as any)[rowKey] : index}>
          <H5PoolsCard
            key={item?.id}
            item={item}
            columns={columns}
            loading={loading}
            isOpen={openPoolsGroup?.[item?.id]}
            onOpen={onOpenPoolsGroup}
            goLiquidity={goLiquidity}
            itemSkeletonLength={itemSkeletonLength}
            itemHeight={itemHeight}
          />
          {isShowBorder && haveDividingLine && <Box w="100%" h="1px" bg="border" />}
        </VStack>
      ))}
      {pagination && pagination?.total > pagination?.size && (
        <Center mt="16px">
          <Pagination {...pagination} />
        </Center>
      )}
    </VStack>
  )
}
export default H5Pools
