import useCommonGlobalStore from '@/store/common/global'
import { DlmmApiPoolGroupItem } from '@/types/dlmm'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Icon } from '@cetus/ui-kit'
import { cancelBubble, d } from '@cetus/utils'
import { Box, Button, Center, Collapse, Flex, Text, VStack } from '@chakra-ui/react'
import { useMemo, useState } from 'react'

const PoolsItem = ({
  item,
  columns,
  goLiquidity,
  isOpen = false,
  onOpen
}: {
  item: DlmmApiPoolGroupItem
  columns: any[]
  goLiquidity: (url: string, poolApiInfo: any) => void
  isOpen?: boolean
  onOpen: (groupId: string, isOpen: boolean) => void
}) => {
  const [showAll, setShowAll] = useState(false)
  const { setBackUrl } = useCommonGlobalStore()
  const onExpand = (e: any) => {
    cancelBubble(e)
    onOpen(item?.id, !!!isOpen)
    if (!isOpen) {
      setShowAll(false)
    }
  }
  const onShowAll = (e: any) => {
    cancelBubble(e)
    setShowAll(!showAll)
  }

  const allData = item?.list
  const lessData = item?.list?.slice(0, 3)

  const { isApp } = useWindowWidth()

  const isOnlyOneData = useMemo(() => allData?.length === 1, [allData?.length])

  const onClick = (e: any) => {
    if (isOnlyOneData) {
      setBackUrl('/pools?tab=dlmm_pools')
      goLiquidity(`/dlmm?poolId=${allData?.[0]?.poolId}`, item)
    } else {
      onExpand(e)
    }
  }

  return (
    <VStack
      border="1px solid"
      borderColor={isOpen ? 'border' : 'transparent'}
      borderRadius="12px"
      gap="0"
      bg={isOpen ? 'bg_secondary' : 'transparent'}
      className="dlmm-tutorial-step-1"
      mb={isOpen ? '12px' : '0'}
    >
      <Box
        pos="relative"
        onClick={onClick}
        cursor="pointer"
        _hover={{
          '.dlmm_action': {
            svg: { fill: 'text_caption' },
            p: { color: 'text_caption' }
          },
          bg: isApp ? 'auto' : 'primary_opacity.10',
          borderRadius: '12px',
          '.dlmm_fee_and_bin_step': {
            borderColor: 'token_inactive_border'
          },
          '.dlmm_action_pools': {
            borderColor: 'token_inactive_border'
          },
          '.dlmm_pools_arrow': {
            '.dlmm_pools_arrow_ani_4': {
              animationIterationCount: 1
            },
            '.dlmm_pools_arrow_ani_3': {
              animationIterationCount: 1
            },
            '.dlmm_pools_arrow_ani_2': {
              animationIterationCount: 1
            },
            '.dlmm_pools_arrow_ani_1': {
              animationIterationCount: 1
            }
          }
        }}
        as="div"
        display="grid"
        w="100%"
        h="76px"
        p="0 12px"
        borderRadius="12px 12px 0 0"
        columnGap="12px"
        bg={isOpen ? 'white_color_opacity.5' : 'transparent'}
        gridTemplateColumns="1fr 128px 128px 128px 90px 120px 128px"
      >
        {isOpen && (
          <Box
            position="absolute"
            top="76px"
            left="28px"
            h={`${d(60)
              .mul(showAll ? allData?.length - 0.5 : lessData?.length - 0.5)
              .plus(9)
              .toString()}px`}
            w="1px"
            bg="border"
          />
        )}

        {columns?.map((col, index) => (
          <Flex align="center" key={col.key} justify={index === 0 ? 'flex-start' : 'flex-end'}>
            {col?.render ? (
              col?.render({
                ...item,
                isOpen,
                list: item?.list,
                onExpand: onClick,
                hasFee: isOnlyOneData,
                isParent: true,
                ...(isOnlyOneData ? allData?.[0] : {})
              })
            ) : (
              <Text fontSize="14px" fontWeight="500" color="text_caption">
                {item?.[col?.key] || '--'}
              </Text>
            )}
          </Flex>
        ))}
      </Box>
      {!isOnlyOneData && (
        <Collapse
          in={isOpen}
          animateOpacity
          transition={{ enter: { duration: 0.8 } }}
          style={{
            width: '100%'
          }}
        >
          <>
            <VStack w="100%" gap="8px">
              <Box h="1px" />
              <VStack w="100%" gap="0px">
                <Collapse
                  startingHeight={60 * lessData?.length}
                  in={showAll}
                  style={{
                    width: '100%'
                  }}
                >
                  {allData?.map((i, index) => {
                    return (
                      <Box w="100%" p="0 8px" borderRadius="12px" pos="relative" key={i.poolId}>
                        <Box w="100%" h="60px" pl="32px">
                          <Box
                            h="100%"
                            className="dlmm-tutorial-step-2"
                            as="div"
                            display="grid"
                            w="100%"
                            // bg="text_highlight_opacity.10"
                            borderRadius="12px"
                            p="0 6px"
                            onClick={() => {
                              setBackUrl('/pools?tab=dlmm_pools')
                              goLiquidity(`/dlmm?poolId=${i.poolId}`, item)
                            }}
                            cursor="pointer"
                            _hover={{
                              bg: 'primary_opacity.10',
                              '.dlmm_fee_and_bin_step': {
                                borderColor: 'token_inactive_border'
                              },
                              '.dlmm_action_pools': {
                                borderColor: 'token_inactive_border'
                              }
                            }}
                            columnGap="12px"
                            gridTemplateColumns="1fr 128px 128px 128px 90px 120px 128px"
                          >
                            {columns?.map((col, index) => (
                              <Flex align="center" key={col.key} justify={index === 0 ? 'flex-start' : 'flex-end'}>
                                {col?.render ? (
                                  col?.render({ ...i, hasFee: true })
                                ) : (
                                  <Text fontSize="14px" fontWeight="500" color="text_caption">
                                    {i?.[col?.key] || '--'}
                                  </Text>
                                )}
                              </Flex>
                            ))}
                          </Box>
                        </Box>
                        <Box pos="absolute" w="12px" h="1px" bg="border" top="50%" left="28px" />
                      </Box>
                    )
                  })}
                </Collapse>
              </VStack>
            </VStack>
            {allData?.length > 3 ? (
              <Center w="100%">
                <Button
                  h="44px"
                  variant="unstyled"
                  onClick={onShowAll}
                  display="flex"
                  gap="4px"
                  cursor="pointer"
                  _hover={{
                    p: {
                      color: 'primary'
                    },
                    svg: {
                      fill: 'primary'
                    }
                  }}
                >
                  <Text fontSize="12px">{showAll ? 'Show Less' : 'Show All'}</Text>
                  <Icon xlinkHref="#icon-icon_arrow" transform={showAll ? 'rotate(180deg)' : 'rotate(0deg)'} fontSize="12px" />
                </Button>
              </Center>
            ) : (
              <Box h="9px" w="100%" />
            )}
          </>
        </Collapse>
      )}
    </VStack>
  )
}
export default PoolsItem
